.simple-page-scroller{
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    overflow-y: auto;
    height: 80vh;
  }
  
  .draggable-item{
    height: 50px;
    line-height: 50px;
    text-align: center;
    width : 100%;
    display: block;
    background-color: #fff;
    outline: 0;
    border: 1px solid rgba(0,0,0,.125);
    margin-bottom: 2px;
    margin-top: 2px;
  }